  .radio-tile-group {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
  }
  .bg-honeydew {
    background-color: hwb(186 24% 10%); /* Hexadecimal color code for honeydew */
  }

  .radio-tile-group .input-container {
      position: relative;
      margin: 0.5rem;
  }

  #selectWalletModal .radio-tile-group .input-container {
      position: relative;
      margin: 0.5rem;
  }

  .radio-tile-group .input-container .radio-button {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      margin: 0;
      cursor: pointer;
      height: 100%;
      width: 100%;
  }

  .radio-tile-group .input-container .radio-tile {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      border: 2px solid #079ad9;
      border-radius: 5px;
      transition: transform 100ms ease;
  }

  #selectWalletModal .radio-tile-group .input-container .radio-tile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 2px solid #079ad9;
      border-radius: 5px;
      transition: transform 100ms ease;
      padding: 1rem;
  }

  .radio-tile-group .input-container .radio-tile .icon svg,
  .radio-tile-group .input-container .radio-tile .icon img {
      fill: #079ad9;
      width: 3rem;
      height: 3rem;
  }

  #selectWalletModal .radio-tile-group .input-container .radio-tile .icon svg,
  #selectWalletModal .radio-tile-group .input-container .radio-tile .icon img {
      fill: #079ad9;
      min-width: 3rem;
      min-height: 3rem;
  }

  .radio-tile-group .input-container .radio-tile .radio-tile-label {
      text-align: center;
      font-size: 0.75rem;
      font-weight: 600;
      letter-spacing: 1px;
  }

  #selectWalletModal .radio-tile-group .input-container .radio-tile .radio-tile-label {
      text-transform: none;
  }

  #selectWalletModal .radio-tile-group .input-container .radio-tile .radio-tile-label {
      color: #079ad9;
  }

  .radio-tile-group .input-container .radio-button:checked+.radio-tile {
      background-color: #079ad972;
      border: 2px solid #079ad9;
      color: white;
      transform: scale(1.1, 1.1);
  }

  .radio-tile-group .input-container .radio-button:checked+.radio-tile .icon svg {
      fill: white;
      /* background-color: #079ad9; */
  }

  .radio-tile-group .input-container .radio-button:checked+.radio-tile .radio-tile-label {
      color: white;
      /* background-color: #079ad9; */
  }

  #startWritingForm label {
      font-size: 1rem;
  }

  #startWritingForm .form-label,
  #startWritingForm input,
  #startWritingForm select {
      font-size: 1.2rem;
      text-transform: none;
      font-weight: 600;
      letter-spacing: .2px;
      color: #fff;
  }
  .invalid-address {
    outline: 2px solid red;
  }
  

  #startWritingForm .form-label span {
      color: darkgray;
  }

  #startWritingForm button {
      background: linear-gradient(199.76deg, #576DB9 26.21%, rgba(143, 141, 231, 0) 100.38%);
      background-blend-mode: lighten;
      border-radius: 20px;
      color: #fff;
      padding: 1rem 3rem;
  }

  #startWritingForm button,
  #startWritingForm button :focus {
      padding: 8px 20px;
      margin-left: 30px;
      border-radius: 50px;
      color: #fff;
      font-family: 'Inter';
      font-style: normal;
      font-size: 1.2rem;
      border: 2px solid #47b2e4;
      font-weight: 500;
  }

  #startWritingForm select option {
      background-color: transparent;
  }
  #back:hover {
    /* Add your desired styles for the hover effect here */
    background-color: #FFD700; /* Set the background color to golden */
    color: #000000; /* Set the text color to black */
  }.startwritingcard {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      opacity: 0;
      animation: cardAnimation 1.5s ease-in-out forwards;
      transform: scale(0);
      transform-origin: center;
    }
    
    @keyframes cardAnimation {
      0% {
        opacity: 0;
        transform: scale(0);
      }
      100% {
        opacity: 1;
        transform: scale(1);
      }
    }
    
    .startwritingcard button,label {
      opacity: 0;
      transform: scale(0.5);
      animation: buttonAnimation 2.5s ease-in-out forwards;
    }
    
    @keyframes buttonAnimation {
      0% {
        opacity: 0;
        transform: scale(0.5);
      }
      100% {
        opacity: 1;
        transform: scale(1);
      }
    }
    