@keyframes slideIn {
  0% {
    opacity: 0;
    /* transform: translateX(-100%); */
  }
  100% {
    opacity: 1;
    /* transform: translateX(0); */
  }
}

h1 {
  opacity: 0;
}

h1.visible {
  animation-name: slideIn;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

h1.visible span {
  display: inline-block;
  animation-name: slideIn;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-delay: 0s;
  opacity: 0;
}

h1.visible span.space {
  display: inline;
}

h1.visible span:nth-child(0) {
  animation-delay: 0s;
}

h1.visible span:nth-child(1) {
  animation-delay: 0.25s;
}

h1.visible span:nth-child(2) {
  animation-delay: 0.5s;
