* {
    margin: 0;
    padding: 0
  }
  
  body {
    background-color: #000
  }
  
  .card {
    width: 350px;
    background-color: #181b3d;
    border: none;
    cursor: pointer;
    transition: all 0.5s;
  }
  
  .image img {
    transition: all 0.5s
  }
  
  .card:hover .image img {
    transform: scale(1.1)
  }
  
  .profile-bg {
    border-radius: 25%
  }
  #connect{
    color: white;
  }
  
  .name {
    font-size: 22px;
    font-weight: bold
  }
  
  .idd {
    font-size: 14px;
    font-weight: 600
  }
  
  .idd1 {
    font-size: 12px
  }
  
  .number {
    font-size: 22px;
    font-weight: bold
  }
  
  .follow {
    font-size: 12px;
    font-weight: 500;
    color: #444444
  }
  
  .btn1 {
    height: 40px;
    width: 150px;
    border: none;
    background-color: #000;
    color: #aeaeae;
    font-size: 15px
  }
  
  .text span {
    font-size: 13px;
    color: #545454;
    font-weight: 500
  }
  
  .icons i {
    font-size: 19px
  }
  
  hr .new1 {
    border: 1px solid
  }
  
  .join {
    font-size: 14px;
    color: #a0a0a0;
    font-weight: bold
  }
  
  .date {
    background-color: #ccc
  }
  
  .card-header {
    width: 100%;
  }
  
  .permissions {
    /* border: 1px solid #ccc; */
    border-radius: 4px;
    background-color: #30344c;
    padding: 10px;
  }
  
  .permissions .correct-symbol {
    display: inline-block;
    width: 20px;
    height: 20px;
    /* background-color: green; */
    color: #05797b;
    text-align: center;
    line-height: 20px;
    font-size: 14px;
    border-radius: 50%;
    margin-right: 5px;
  }
  #back:hover {
  /* Add your desired styles for the hover effect here */
  background-color: #FFD700; /* Set the background color to golden */
  color: #000000; /* Set the text color to black */
  }.writeherecard {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    opacity: 0;
    animation: cardAnimation 3s ease-in-out 1s forwards;
    transform: scale(0);
    transform-origin: center;
  }
  
  @keyframes cardAnimation {
    0% {
      opacity: 0;
      transform: scale(0);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  .writeherecard button {
    opacity: 0;
    transform: scale(0.5);
    animation: buttonAnimation 3s ease-in-out 1.2s forwards;
  }
  
  .writeherecard button:nth-child(1) {
    animation-delay: 1.2s;
  }
  
  .writeherecard button:nth-child(2) {
    animation-delay: 1.5s;
  }
  
  .writeherecard button:nth-child(3) {
    animation-delay: 1.8s;
  }
  
  .writeherecard button:nth-child(4) {
    animation-delay: 2.1s;
  }
  
  .writeherecard button:nth-child(5) {
    animation-delay: 2.4s;
  }
  .writeherecard button:nth-child(6) {
    animation-delay: 2.7s;
  }
  
  .writeherecard button:nth-child(7) {
    animation-delay: 3s;
  }
  
  .writeherecard button:nth-child(8) {
    animation-delay: 3.3s;
  }
  /* .spl button{
    opacity: 0;
    transform: scale(0.5);
    animation: buttonAnimation 3s ease-in-out 5s forwards;
  } */
  @keyframes buttonAnimation {
    0% {
      opacity: 0;
      transform: scale(0);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }